<template>
	<view class="content">
		<view class="uni-flex uni-column">
			<view class="logo flex-item"></view>
			<view class="title flex-item">欢迎使用奥力格</view>
			<view class="login flex-item">申请账号</view>
			<view class="flex-item">
				<form @submit="formSubmit">

					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" v-model="phone" placeholder="请输入姓名" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="password" v-model="password" placeholder="请输入手机号" />
					</view>
					
					<view class="uni-btn-v">
						<button class="submit" form-type="submit">提交</button>
					</view>
					<view class="apply uni-btn-v">
						<text class="remarks">备注:由于我们的会员是邀月制的,您提交信息后</text>
						<text class="remarks">工作人员会尽快与您联系</text>
					</view>
				</form>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:'',
				password:'',
			}
		},
		onLoad() {

		},
		methods: {
			formSubmit: function(e) {
				console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
				uni.request({
				    url: 'http://www.aolige.cn/apis/js/a/login', //仅为示例，并非真实接口地址。
					method:'POST',
				    data: {
				        username:this.phone,
				        password:this.password,
				    },
				    header: {
				        'custom-header': 'hello' //自定义请求头信息
				    },
				    success: (res) => {
				        console.log(res.data);
				    }
				});
			},
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 20px;
		box-sizing: border-box;
	}
	.logo{
		width: 36px;
		height: 36px;
		padding: 100rpx 0 20rpx 0;
		background: url(../../static/logo.png) no-repeat 100%;
	}
	.title{
		font-size: 50rpx;
		font-weight: 700;
		padding-bottom: 100rpx;
	}
	.login{
		width: 155rpx;
		padding: 0 0 5rpx 0;
		border-bottom: 3px solid #007AFF;
	}
	.uni-input{
		border-bottom: 1px solid #DCDEE0;
		padding: 20rpx 0;
		margin: 40rpx 0 ;
	}
	.submit{
		margin-top: 80rpx;
		background-color: #007AFF;
		color: #FFFFFF;
	}
	.apply{
		margin-top: 80rpx;
		text-align: center;
	}
	.remarks{
		display: block;
		text-align: center;
		/* margin: 0 30rpx; */
		font-size: 14px;
		color: orange;
	}
</style>
